import React, { useState } from "react";
import s from "./Pagination.module.css";

const Pagination = ({
  onChangePage,
  totalItems,
  countItemsOnPage,
  currentPage,
  portionSize = 10,
}) => {
  const pagesCount = Math.ceil(totalItems / countItemsOnPage);

  let pages = [];

  for (let i = 1; i <= pagesCount; i++) {
    pages.push(i);
  }

  const portionsCount = Math.ceil(pagesCount / portionSize);
  const currentPortion = Math.ceil(currentPage / portionSize);
  const [portionNumber, setPortionNumber] = useState(currentPortion);
  const leftPortionPageNumber = (portionNumber - 1) * portionSize + 1;
  const rightPiortionPageNumber =
    (portionNumber - 1) * portionSize + portionSize;

  return (
    <div className={s.pagination}>
      {portionNumber > 1 && (
        <button
          onClick={() => {
            setPortionNumber(portionNumber - 1);
          }}
        >
          Prev
        </button>
      )}

      {pages
        .filter(
          (p) => p >= leftPortionPageNumber && p <= rightPiortionPageNumber
        )
        .map((pageNumber) => (
          <span
            onClick={() => {
              onChangePage(pageNumber);
            }}
            className={currentPage === pageNumber && s.currentPage}
          >
            {pageNumber}
          </span>
        ))}

      {portionNumber < portionsCount && (
        <button
          onClick={() => {
            setPortionNumber(portionNumber + 1);
          }}
        >
          Next
        </button>
      )}
    </div>
  );
};

export default Pagination;
