import React from "react";
import { connect } from "react-redux";
import {
  follow,
  unfollow,
  setCurretPage,
  getCurrentPageUsers,
} from "../../redux/users-reducer";

import Users from "./Users";
import Preloader from "../common/preloader/Preloader";
import { compose } from "redux";
import { getCountUsersOnPage, getCurrentPage, getFollowingInProcess, getIsFetching, getTotalUsers, getUsers } from "../../redux/users-selector";

class UsersContainer extends React.Component {
  componentDidMount() {
    this.props.getCurrentPageUsers(
      this.props.currentPage,
      this.props.countUsersOnPage
    );
  }

  onChangePage = (page) => {
    this.props.setCurretPage(page);
    this.props.getCurrentPageUsers(page, this.props.countUsersOnPage);
  };
  render = () => {
    return (
      <>
        {this.props.isFetching ? (
          <Preloader />
        ) : (
          <Users
            currentPage={this.props.currentPage}
            totalUsers={this.props.totalUsers}
            countUsersOnPage={this.props.countUsersOnPage}
            onChangePage={this.onChangePage}
            users={this.props.users}
            follow={this.props.follow}
            unfollow={this.props.unfollow}
            followingInProcess={this.props.followingInProcess}
          />
        )}
      </>
    );
  };
}

const mapStateToProps = (state) => {
  return {
    users: getUsers(state),
    totalUsers: getTotalUsers(state),
    currentPage: getCurrentPage(state),
    countUsersOnPage: getCountUsersOnPage(state),
    isFetching: getIsFetching(state),
    followingInProcess: getFollowingInProcess(state),
  };
};

export default compose(
  connect(mapStateToProps, {
    follow,
    unfollow,
    setCurretPage,
    getCurrentPageUsers,
  })
)(UsersContainer);
