import React from "react";
import { NavLink } from "react-router-dom";
import s from "./user.Module.css";

const User = ({ user, followingInProcess, follow, unfollow }) => {
  return (
    <div className={s.user}>
      <div>
        <div className={s.ava}>
          <NavLink to={"/profile/" + user.id}>
            <img
              src={
                user.photos.small
                  ? user.photos.small
                  : "https://www.wired.com/wp-content/uploads/2015/09/10093860866_803211264e_o.jpg"
              }
              alt="ava"
            />
          </NavLink>
        </div>
        <div>
          {user.followed ? (
            <button
              disabled={followingInProcess.some((id) => id === user.id)}
              onClick={() => {
                unfollow(user.id);
              }}
            >
              unfollow
            </button>
          ) : (
            <button
              disabled={followingInProcess.some((id) => id === user.id)}
              onClick={() => {
                follow(user.id);
              }}
            >
              follow
            </button>
          )}
        </div>
      </div>
      <div>
        <div>
          <div>{user.name}</div>
          <div>{user.status ? user.status : "no status"}</div>
        </div>
        <div>
          <div>{"user.location.country"}</div>
          <div>{"user.location.city"}</div>
        </div>
      </div>
    </div>
  );
};

export default User;
